/* ON/OFF SWTICHER */
.switch {
  width: 50px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.switch input {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

.switch label {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #cbc7bc;
  box-shadow: inset 0px 1px 0px white,
  0 0 0 1px #999,
  0 0 5px 1px rgba(0,0,0,.2),
  0 2px 0 rgba(255,255,255,.6),
  inset 0 11px 0 rgba(255,255,255,.5),
  inset 0 -45px 3px #ddd;
}

.switch label::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: -13px;
  left: 20px;
  background-color: #666;
  border-radius: 50%;
  box-shadow:
    0 1px 0 white,
    0 120px 0 #666,
    0 121px 0 white;
}

.switch label::after {
  content: "";
  position: absolute;
  top: -20px;
  left: -25px;
  bottom: -20px;
  right: -25px;
  background-color: #ccc;
  z-index: -1;
  background: -webkit-linear-gradient(#ddd, #bbb);
  background: linear-gradient(#ddd, #bbb);
  border-radius: 5px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, .15),
              0 0 3px 13px rgba(0, 0, 0, .3)
}

.switch input:checked ~ label {
  background-color: #d2cbc3;
  box-shadow: inset 0 1px 0 white,
              0 0 0 1px #999,
              0 0 5px 1px rgba(0,0,0,.2),
              inset 0 -10px 0 #aaa,
              0 2px 0 rgba(255,255,255,.1),
              inset 0 45px 3px #e0e0e0;
              
}


