  from{width: 0;}
  to{width: 100%;}
  }
@keyframes typewriter{
  from{width: 0;}
  to{width: 100%;}
  }
@-webkit-keyframes blinkTextCursor{
  from{border-right-color: rgba(116, 109, 109, 0.75);}
  to{border-right-color: transparent;}
  }
@keyframes blinkTextCursor{
  from{border-right-color: rgba(116, 109, 109, 0.75);}
  to{border-right-color: transparent;}
  }

/* home page image flicker in */
.flicker-in-2 {
	-webkit-animation: flicker-in-2 2.5s linear both;
          animation: flicker-in-2 2.5s linear both;
          -webkit-animation-delay: 2s;
                  animation-delay: 2s;
}


